// src/components/auth/VerifyEmail.jsx
import React, { useEffect, useState, useRef, useMemo } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import api from "../../utils/api";

export default function VerifyEmail() {
  const location = useLocation();
  const navigate = useNavigate();
  const [status, setStatus] = useState("loading"); // 'loading' | 'success' | 'error'
  const [error, setError] = useState("");
  const verificationAttempted = useRef(false);

  //   ?token= query string
  const token = useMemo(
    () => new URLSearchParams(location.search).get("token"),
    [location.search]
  );

  useEffect(() => {
    const verifyEmail = async () => {
      // Prevent multiple calls for same token
      if (verificationAttempted.current) return;
      verificationAttempted.current = true;

      if (!token) {
        setStatus("error");
        setError("   .     .");
        return;
      }

      try {
        //  GET  queryparam
        await api.get(`/api/auth/verify-email/${encodeURIComponent(token)}`);

        // 
        setStatus("success");
        //    URL
        window.history.replaceState(
          {},
          document.title,
          window.location.pathname
        );
        //     2 
        setTimeout(() => navigate("/login", { replace: true }), 2000);
      } catch (err) {
        console.error("Verification error:", err);
        const resp = err.response;
        // special case:   
        const msg = resp?.data?.message || resp?.data?.error;
        if (
          resp?.status === 400 &&
          (msg === "Token already used" || msg === "Email already verified")
        ) {
          setStatus("success");
          window.history.replaceState(
            {},
            document.title,
            window.location.pathname
          );
          setTimeout(() => navigate("/login", { replace: true }), 2000);
          return;
        }

        //   
        setStatus("error");
        if (resp) {
          if (resp.status === 400) {
            setError(" .     .");
          } else if (resp.status === 401) {
            setError("  .     .");
          } else {
            setError(msg || "   .");
          }
        } else if (err.request) {
          setError("  .     .");
        } else {
          setError("   .     .");
        }
      }
    };

    verifyEmail();
  }, [token, location.search, navigate]);

  if (status === "loading") {
    return <div className="text-center p-4"> ...</div>;
  }
  if (status === "success") {
    return (
      <div className="text-center p-4">
        <h2>  !</h2>
        <p>   </p>
      </div>
    );
  }
  // status === "error"
  return (
    <div className="text-center p-4">
      <h2> </h2>
      <p>{error}</p>
      <button
        type="button"
        onClick={() => navigate("/login", { replace: true })}
        className="mt-4 form-button"
      >
         
      </button>
    </div>
  );
}
