// src/components/auth/SignupForm.jsx
import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import api from "../../utils/api";
import teamColors from "../../utils/teamStyles";
import AuthHeader from "./AuthHeader";
import "../../styles/auth.css";

const SignupForm = () => {
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [location, setLocation] = useState("");
  const [phone, setPhone] = useState("");
  const [gender, setGender] = useState("");
  const [birthDate, setBirthDate] = useState("");
  const [favoriteTeam, setFavoriteTeam] = useState("");
  const [bio, setBio] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");


  const [isLoading, setIsLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState("");

  const locations = ["", "", "", "", ""];

  const navigate = useNavigate();

  const handleSignup = async (e) => {
    e.preventDefault();
    setError("");
    if (password !== confirmPassword) {
      setError("  .");
      return;
    }
    setIsLoading(true);
    try {
      await api.post("/api/auth/register", {
        email,
        name,
        location,
        phone,
        gender,
        birthDate,
        favoriteTeam,
        bio,
        password,
      });
      setSuccess(true);
    } catch (err) {
      console.error("Signup error:", err);
      setError(err.response?.data?.error || " .");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="auth-container">
      {success ? (
        <div className="form-container">
          <div className="form-content">
            <div className="form-header">
              <h2 className="decorative-title"> !</h2>
              <p className="decorative-text">
                   .   .
              </p>
            </div>
            <button className="form-button" onClick={() => navigate("/login")}>
              
            </button>
          </div>
        </div>
      ) : (
        <div className="form-container">
          <div className="form-content">
            <AuthHeader
              title=" !"
              subtitle="   ,     ."
            />

            {error && <p className="form-error">{error}</p>}

            <form onSubmit={handleSignup} className="form">
              {/*   */}
              <div className="form-group">
                <label htmlFor="fullName" className="form-label">
                   
                </label>
                <input
                  id="fullName"
                  type="text"
                  className="form-input"
                  placeholder="  "
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  required
                />
              </div>

              {/*  */}
              <div className="form-group">
                <label htmlFor="email" className="form-label">
                  
                </label>
                <input
                  id="email"
                  type="email"
                  className="form-input"
                  placeholder="you@example.com"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                />
              </div>
              {/**/}
              <div className="form-group">
                <label htmlFor="password" className="form-label">
                  
                </label>
                <input
                  id="password"
                  type="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                  className="form-input"
                  placeholder=" "
                />
              </div>

              <div className="form-group">
                <label htmlFor="password" className="form-label">
                   
                </label>
                <input
                  type="password"
                  placeholder="  "
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  className="form-input"
                  required
                />
              </div>

              {/*  */}
              <label htmlFor="location" className="form-label">
                 
              </label>
              <select
                id="location"
                value={location}
                onChange={(e) => setLocation(e.target.value)}
                required
                className="form-select"
              >
                {locations.map((loc) => (
                  <option key={loc} value={loc}>
                    {loc}
                  </option>
                ))}
              </select>

              {/*  */}
              <div className="form-group">
                <label htmlFor="phone" className="form-label">
                  
                </label>
                <input
                  id="phone"
                  type="tel"
                  className="form-input"
                  placeholder="050-1234567"
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  required
                />
              </div>

              {/*  */}
              <div className="form-group">
                <label htmlFor="gender" className="form-label">
                  
                </label>
                <select
                  id="gender"
                  className="form-select"
                  value={gender}
                  onChange={(e) => setGender(e.target.value)}
                  required
                >
                  <option value=""> </option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>

              {/*   */}
              <div className="form-group">
                <label htmlFor="birthDate" className="form-label">
                   
                </label>
                <input
                  id="birthDate"
                  type="date"
                  className="form-input"
                  value={birthDate}
                  onChange={(e) => setBirthDate(e.target.value)}
                  required
                />
              </div>

              {/*   */}
              <div className="form-group">
                <label htmlFor="favoriteTeam" className="form-label">
                   
                </label>
                <select
                  id="favoriteTeam"
                  className="form-select"
                  value={favoriteTeam}
                  onChange={(e) => setFavoriteTeam(e.target.value)}
                  required
                >
                  <option value=""> </option>
                  {Object.keys(teamColors).map((key) => (
                    <option key={key} value={key}>
                      {key}
                    </option>
                  ))}
                </select>
              </div>

              {/*  */}
              <div className="form-group">
                <label htmlFor="bio" className="form-label">
                   
                </label>
                <textarea
                  id="bio"
                  className="form-input"
                  rows={3}
                  placeholder="    "
                  value={bio}
                  onChange={(e) => setBio(e.target.value)}
                />
              </div>

              {/*   */}
              <button
                type="submit"
                className="form-button"
                disabled={isLoading}
              >
                {isLoading ? "..." : ""}
              </button>
            </form>

            {/*   */}
            <div className="form-footer">
              <p>
                   ?{" "}
                <Link to="/login" className="form-link">
                   
                </Link>
              </p>
            </div>
          </div>
        </div>
      )}

      {/*        */}
      <div className="decorative-container">
        <div className="field-pattern"></div>
        <div className="field-elements">
          <div className="center-circle"></div>
          <div className="center-dot"></div>
          <div className="halfway-line"></div>
          <div className="decorative-column">
            <div className="decorative-card">
              <h2 className="decorative-title">  !</h2>
              <p className="decorative-text">
                   ,     ,   
                     .
              </p>
            </div>
            <div className="field-pattern"></div>
          </div>
          <div className="penalty-area-top"></div>
          <div className="penalty-area-bottom"></div>
          <div className="goal-top"></div>
          <div className="goal-bottom"></div>
        </div>
      </div>
    </div>
  );
};

export default SignupForm;
